const format = (elemTime, duration) => {
    const currTime = elemTime;
    const durationTime = duration;
    const minutes = currTime / 60;
    let m = minutes.toString().substr(0, minutes.toString().indexOf(".")) || 0;
    let s = Math.round(currTime % 60).toString() || 0;
    s = parseInt(s) < 10 ? `0${s}` : s;
    m = parseInt(m) < 10 ? `0${m}` : m;
    const timePercentage = (currTime / durationTime) * 100 || null;

    return { 
        s,
        m,
        timePercentage
     }
};

export default format;