import React from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

import "./menu.css";
import MenuItem from "../menu-item";

const NextArrow = props => {
    return <i onClick={props.onClick} className='material-icons slider-arrow arrow-next'>keyboard_arrow_right</i>;
};

const PrevArrow = props => {
  return <i onClick={props.onClick} className='material-icons slider-arrow arrow-prev'>keyboard_arrow_left</i>;
};

const Menu = props => {
    var settings = {
        infinite: true,
        initialSlide: props.savedVideo - 1 || 2,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        centerMode: true,
        focusOnSelect: true,
        nextArrow: <NextArrow />,
        prevArrow: <PrevArrow />
    };

    const { data, catchVideoId } = props;
    return (
        <div className='menu'>
            <Slider {...settings}>
                {data.map(item => {
                    return <MenuItem id={item.id} thumb={item.thumb} key={item.id} catchVideoId={catchVideoId} />;
                })}
            </Slider>
            <div className='slider-arrow-bottom'>
                <i className='material-icons'>arrow_drop_up</i>
            </div>
        </div>
    );
};

export default Menu;
