import React from "react";

import "./controls-top.css";

const ControlsTop = props => {

    const { titleData, controlsHidden, isVolumeChange, volume } = props;

    return (
        <div className='controls-top'>
            <div
                className={
                    controlsHidden
                        ? "title-wrapper controls-hide"
                        : "title-wrapper"
                }>
                <div className='title video-title'>{titleData[0]}</div>
                <div className='title video-subtitle'>{titleData[1]}</div>
            </div>

            <div
                className={
                    !isVolumeChange
                        ? "volume-percentage"
                        : "volume-percentage fade"
                }>
                {volume}%
            </div>
        </div>
    );
};

export default ControlsTop;
