import React from "react";

import "./controls-bottom.css";

const ControlsBottom = props => {
    const {
        controlsHidden,
        isMuted,
        isPlaying,
        progressLine,
        volume,
        mute,
        durationTime,
        playVideo,
        playTime,
        recognizeClick,
        setControlsHidden,
        resize,
        hoverProgressLine,
        hoveredTime,
        positionHoveredTime
    } = props;

    return (
        <div
            className={controlsHidden ? "controls-bottom controls-hide" : "controls-bottom"}
            onMouseOut={() => setControlsHidden(true)}
            onMouseOver={() => {
                setTimeout(() => {
                    setControlsHidden(false);
                }, 200);
            }}
        >
            <div className='controls-button play-button'>
                {!isPlaying ? (
                    <i className='material-icons' onClick={() => playVideo()}>
                        play_arrow
                    </i>
                ) : (
                    <i className='material-icons' onClick={() => playVideo()}>
                        pause
                    </i>
                )}
            </div>
            <span className='time start-time'>{playTime}</span>
            <div className='line-wrapper' id='line-wrapper' onClick={e => recognizeClick(e, "progressLine")} onMouseMove={e => hoverProgressLine(e)}>
                <div className={isPlaying ? "line-inner line-inner-ease" : "line-inner"} style={{ width: `${progressLine}%` }}></div>
                <div className='hovered-time' style={{ left: `${positionHoveredTime - 20}px` }}>
                    {hoveredTime}
                </div>
            </div>

            <span className='time end-time'>{durationTime}</span>
            <div className='controls-button volume'>
                {isMuted || volume === 0 ? (
                    <i className='material-icons' onClick={() => mute()}>
                        volume_off
                    </i>
                ) : (
                    <i className='material-icons' onClick={() => mute()}>
                        volume_up
                    </i>
                )}
                <div className='volume-control-wrapper'>
                    <div className='volume-control-inner' id='volume-control-inner' onClick={e => recognizeClick(e, "volume")}>
                        <div className='volume-control' style={{ height: `${volume}%` }} id='volume-control'></div>
                    </div>
                </div>
            </div>
            <div className='controls-button resize'>
                <i className='material-icons' onClick={() => resize()}>
                    fullscreen
                </i>
            </div>
        </div>
    );
};

export default ControlsBottom;
