import React, { useEffect } from "react";

import "./title.css";
import { pinkTheme, redTheme } from "../../themes";

const Title = () => {
    useEffect(() => {
        const savedTheme = localStorage.getItem("theme");
        applyTheme(savedTheme || currentTheme);
    });

    const [currentTheme, setTheme] = React.useState("pink");

    const switchTheme = theme => {
        const newTheme = theme === "pink" ? "pink" : "red";
        localStorage.setItem("theme", newTheme);
        setTheme(newTheme);
        applyTheme(newTheme);
    };

    const applyTheme = nextTheme => {
        const theme = nextTheme === "red" ? pinkTheme : redTheme;
        Object.keys(theme).forEach(key => {
            const value = theme[key];
            document.documentElement.style.setProperty(key, value);
        });
    };

    return (
        <div className='main-title-wrapper'>
            <div className='main-title'>MPlayer</div>
            <button className='btn-switch-theme theme-1' onClick={() => switchTheme("red")}></button>
            <button className='btn-switch-theme theme-2' onClick={() => switchTheme("pink")}></button>
        </div>
    );
};

export default Title;
