import React, { useState, useRef, useEffect } from "react";
import "./player.css";

import format from "../../helpers/format";
import fullscreenOff from "../../helpers/fullscreenOff";
import Controls from "../../modules/controls";


const Player = props => {
    useEffect(() => {
        videoEl.current.onmousemove = () => {
            setControlsHidden(false);
        };

        videoEl.current.oncanplaythrough = () => {
            setCanPlay(true);
        };

        videoEl.current.onloadedmetadata = () => {
            const formattingTime = format(videoEl.current.duration);
            setDurationTime(`${formattingTime.m}:${formattingTime.s}`);
        };

        videoEl.current.onloadeddata = () => {
            const savedCurrentTime = parseInt(localStorage.getItem("savedCurrentTime"));
            videoEl.current.currentTime = savedCurrentTime || 0;
            setCanPlay(true);
        };

        videoEl.current.ontimeupdate = () => {
            const formattingTime = format(videoEl.current.currentTime, videoEl.current.duration);
            updateProgressLine(formattingTime.timePercentage);
            setPlayTime(`${formattingTime.m}:${formattingTime.s}`);

            localStorage.setItem("savedCurrentTime", videoEl.current.currentTime);
        };

        videoEl.current.onended = () => {
            setIsPlaying(false);
        };

        if ((videoEl.current.readyState === 1 && isPlaying) || (videoEl.current.readyState === 2 && isPlaying)) {
            setCanPlay(false);
        } else {
            setCanPlay(true);
        }
    });


    const [isPlaying, setIsPlaying] = useState(false);
    const [isFullScreen, setIsFullScreen] = useState(false);
    const [volume, setVolume] = useState(75);
    const [isMuted, setIsMuted] = useState(false);
    const [isVolumeChange, setIsVolumeChange] = useState(false);
    const [durationTime, setDurationTime] = useState(null);
    const [playTime, setPlayTime] = useState("00:00");
    const [progressLine, updateProgressLine] = useState(0);
    const [isPlayButtonHover, setIsPlayButtonHover] = useState(false);
    const [controlsHidden, setControlsHidden] = useState(false);
    const [canPlay, setCanPlay] = useState(false);
    const [hoveredTime, setHoveredTime] = useState(null);
    const [positionHoveredTime, setPositionHoveredTime] = useState(null);

    const videoEl = useRef(null);
    const player = useRef(null);


    useEffect(() => {
        updateProgressLine(0);
        setIsPlaying(false);
    }, [props.currVideo]);

    useEffect(() => {
        setIsVolumeChange(!isVolumeChange);
        setTimeout(() => {
            setIsVolumeChange(true);
        }, 100);
    }, [volume]);

    const playVideo = () => {
        setIsPlaying(!isPlaying);
        if (isPlaying) {
            videoEl.current.pause();
            setControlsHidden(false);
        } else {
            videoEl.current.play();
            setControlsHidden(true);
        }
    };

    const hoverProgressLine = e => {
        const elemLeft = document.getElementById("line-wrapper").getBoundingClientRect().left;
        const elemRight = document.getElementById("line-wrapper").getBoundingClientRect().right;
        const fullWidth = elemRight - elemLeft;
        const newWidth = e.clientX - elemLeft;
        const time = format((videoEl.current.duration * newWidth) / fullWidth);
        setPositionHoveredTime(newWidth);
        setHoveredTime(`${time.m}:${time.s}`);     
    };

    const resize = () => {
        if (!isFullScreen) {
            const fullscreen =
                player.current.webkitRequestFullscreen ||
                player.current.mozRequestFullScreen ||
                player.current.msRequestFullscreen ||
                player.current.oRequestFullscreen;
            fullscreen.call(player.current);
        } else if (isFullScreen) {
            const fullscreenOff =
                document.webkitExitFullscreen || document.mozExitFullscreen || document.msExitFullscreen || document.oExitFullscreen;
            fullscreenOff.call(document);
        }
        setIsFullScreen(!isFullScreen);
    };

    const mute = () => {
        setIsMuted(!isMuted);
        if (isMuted) {
            videoEl.current.muted = false;
            setVolume(35);
        } else {
            videoEl.current.muted = true;
            setVolume(0);
        }
    };

    const recognizeClick = (e, elem) => {
        if (elem === "volume") {
            const fullVolume = document.getElementById("volume-control-inner").getBoundingClientRect();
            const newVolume = e.clientY;
            const s = fullVolume.top - newVolume;
            const volume = 1 - (s / 85) * -1;
            setVolume(Math.round(volume * 100));
            videoEl.current.volume = volume;
        } else if (elem === "progressLine") {
            const elemLeft = document.getElementById("line-wrapper").getBoundingClientRect().left;
            const elemRight = document.getElementById("line-wrapper").getBoundingClientRect().right;
            const fullWidth = elemRight - elemLeft;
            const newWidth = e.clientX - elemLeft;
            const newPlayTimePercentage = newWidth / fullWidth;
            updateProgressLine(newPlayTimePercentage * 100);
            const newCurrentTime = newPlayTimePercentage * videoEl.current.duration;
            videoEl.current.currentTime = newCurrentTime;
        }
    };

    let timer = 0;
    let prevent = false;

    const handleClick = () => {
        timer = setTimeout(function() {
            if (!prevent) {
                playVideo();
            }
            prevent = false;
        }, 300);
    };
    const handleDoubleClick = () => {
        clearTimeout(timer);
        prevent = true;
        resize();
    };

    const keyAction = e => {
        switch (e.keyCode) {
            case 38:
                e.preventDefault();
                const newVolume1 = volume <= 95 ? volume + 5 : 100;
                setVolume(newVolume1);
                videoEl.current.volume = newVolume1 / 100;
                break;
            case 40:
                e.preventDefault();
                const newVolume2 = volume >= 5 ? volume - 5 : 0;
                setVolume(newVolume2);
                videoEl.current.volume = newVolume2 / 100;
                break;
            case 32:
                setIsPlayButtonHover(false);
                e.preventDefault();
                if (videoEl.current.play) {
                    playVideo();
                }
                break;
            case 39:
                videoEl.current.currentTime = videoEl.current.currentTime + 5;
                break;
            case 37:
                videoEl.current.currentTime = videoEl.current.currentTime - 5;
                break;
            default:
                break;
        }
    };

    fullscreenOff(setIsFullScreen);

    const { currVideo, titleData } = props;

    return (
        <div className='player-block'>
            <div ref={player} onKeyDown={e => keyAction(e)} tabIndex='0' className={isFullScreen ? "fullscreen" : "video-wrapper"}>
                <video ref={videoEl} className='video' style={{ objectFit: "cover" }} src={currVideo}></video>

                <Controls
                    titleData={titleData}
                    canPlay={canPlay}
                    controlsHidden={controlsHidden}
                    isMuted={isMuted}
                    isPlaying={isPlaying}
                    isPlayButtonHover={isPlayButtonHover}
                    isVolumeChange={isVolumeChange}
                    progressLine={progressLine}
                    volume={volume}
                    mute={mute}
                    durationTime={durationTime}
                    playVideo={playVideo}
                    playTime={playTime}
                    recognizeClick={recognizeClick}
                    setControlsHidden={setControlsHidden}
                    setIsPlayButtonHover={setIsPlayButtonHover}
                    resize={resize}
                    handleClick={handleClick}
                    handleDoubleClick={handleDoubleClick}
                    hoverProgressLine={hoverProgressLine}
                    hoveredTime={hoveredTime}
                    positionHoveredTime={positionHoveredTime}
                />
            </div>
        </div>
    );
};

export default Player;
