import React from 'react';

import './controls.css';
import ControlsTop from "./controls-top";
import PauseBigButton from "./pause-big-button";
import ControlsBottom from "./controls-bottom";
import Loader from "../../components/loader";

const Controls = props => {

    const {
        titleData,
        canPlay,
        controlsHidden,
        isMuted,
        isPlaying,
        isPlayButtonHover,
        isVolumeChange,
        progressLine,
        volume,
        mute,
        durationTime,
        playVideo,
        playTime,
        recognizeClick,
        setControlsHidden,
        setIsPlayButtonHover,
        resize,
        handleClick,
        handleDoubleClick,
        hoveredTime,
        positionHoveredTime,
        hoverProgressLine
    } = props;
    
    return (
        <div className='controls'>
                    {!canPlay && <Loader />}
                    <div
                        className='controls-clickable'
                        onMouseMove={() => {
                            setControlsHidden(false);
                            setTimeout(() => {
                                setControlsHidden(true);
                            }, 200);
                        }}
                        onClick={handleClick.bind(this)}
                        onDoubleClick={handleDoubleClick.bind(this)}>
                        <ControlsTop
                            titleData={titleData}
                            controlsHidden={controlsHidden}
                            isVolumeChange={isVolumeChange}
                            volume={volume}
                        />
                        {!isPlaying && (
                            <PauseBigButton
                                playVideo={playVideo}
                                setIsPlayButtonHover={setIsPlayButtonHover}
                                isPlayButtonHover={isPlayButtonHover}
                            />
                        )}
                    </div>

                    <ControlsBottom
                        controlsHidden={controlsHidden}
                        isMuted={isMuted}
                        isPlaying={isPlaying}
                        progressLine={progressLine}
                        volume={volume}
                        mute={mute}
                        durationTime={durationTime}
                        playVideo={playVideo}
                        playTime={playTime}
                        recognizeClick={recognizeClick}
                        setControlsHidden={setControlsHidden}
                        resize={resize}
                        hoverProgressLine={hoverProgressLine}
                        hoveredTime={hoveredTime}
                        positionHoveredTime={positionHoveredTime}
                    />
                </div>
    )
}

export default Controls;