import React from "react";

import "./pause-big-button.css";

const PauseBigButton = props => {

    const { setIsPlayButtonHover, playVideo, isPlayButtonHover } = props;

    return (
        <div className='pause-button-big-wrapper'>
            {isPlayButtonHover ? (
                <i
                    className='material-icons pause-button-big hovered'
                    onClick={() => {
                        playVideo();
                        setIsPlayButtonHover(false);
                    }}
                    onMouseLeave={() => setIsPlayButtonHover(false)}>
                    play_circle_filled
                </i>
            ) : (
                <i
                    className='material-icons pause-button-big'
                    onMouseEnter={() => setIsPlayButtonHover(true)}>
                    play_circle_outline
                </i>
            )}
        </div>
    );
};

export default PauseBigButton;
