import React, { useState } from "react";

import "./app.css";
import Menu from "./components/menu";
import Player from "./components/player";
import Title from "./components/title";
import testData from "./testData.json";

function App() {
    const savedVideo = parseInt(localStorage.getItem("savedVideo"));

    const [currVideo, setCurrVideo] = useState(testData.data[savedVideo - 1 || 2].sources);
    const [titleData, setTitleData] = useState([testData.data[savedVideo - 1 || 2].title, testData.data[savedVideo - 1 || 2].subtitle]);

    const catchVideoId = id => {
        testData.data.forEach(item => {
            if (item.id === id) {
                setCurrVideo(item.sources);
                setTitleData([item.title, item.subtitle]);
                localStorage.setItem("savedVideo", id);
            }
        });
    };

    return (
        <div className='app'>
            <Title />
            <Menu data={testData.data} catchVideoId={catchVideoId} savedVideo={savedVideo} />
            <Player currVideo={currVideo} titleData={titleData} />
        </div>
    );
}

export default App;
